import { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";

function ItemCount({ stock = 0, initial, onAdd }) {
  const [items, setState] = useState({
    stock: stock,
    initial: stock > 0 ? 1 : 0,
  });

  const addItem = () => {
    if (items.initial < items.stock) {
      setState({ ...items, initial: items.initial + 1 });
    }
  };

  const removeItem = () => {
    if (items.initial > 1) {
      setState({ ...items, initial: items.initial - 1 });
    }
  };

  let btnColorAdd =
    items.initial >= items.stock ? "btn-secondary" : "btn-primary";

  let btnColorLess = items.initial === 1 ? "btn-secondary" : "btn-danger";

  return (
    <>
      <div className="bg-light gap-2 p-2 rounded d-flex flex-column justify-content-center">
        <div className="d-flex gap-3 align-items-center group ">
          <div
            className={`btn ${btnColorLess}`}
            onClick={() => {
              removeItem();
            }}
          >
            -
          </div>
          <div className="text-center">{items.initial}</div>
          <div
            className={`btn ${btnColorAdd}`}
            onClick={() => {
              addItem();
            }}
          >
            +
          </div>
        </div>
        <div className="col-auto btn btn-success">Add to cart</div>
      </div>
    </>
  );
}

export default ItemCount;
